import React,{useContext} from "react";
import './App.css';
import UserContext from "./usercontext";



function EditUser(){
    let users = useContext(UserContext)
    return <>
    <h1> User</h1>
    {
        
        console.log(users.userList)
    }
        <form>
            <div class="form-group">
                <label for="exampleInputEmail1">Name</label>
                <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Your Name" value={`${users.userList[0].userName}`}/>

            </div>
            <div class="form-group">
                <label for="exampleInputEmail1">Occupation</label>
                <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Your Occupation" value={`${users.userList[0].userOccupation}`}/>
            </div>
            <div class="form-group">
                <label for="exampleInputEmail1">Email address</label>
                <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" value={`${users.userList[0].userEmail}`} />
                <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
            </div>
            <div class="form-group">
                <label for="exampleInputPassword1">Phone No.</label>
                <input type="number" class="form-control" id="exampleInputPassword1" placeholder="Enter Your Phone No." value={`${users.userList[0].userPhone}`} />
            </div>

            <button type="submit" class="btn btn-primary">Update</button>
        </form>
 </>
}

export default EditUser;