import React,{useContext} from "react";
import { Link } from "react-router-dom";
import UserContext from "./usercontext";

function User() {
    let users = useContext(UserContext)
    return <>
        <div class="d-sm-flex align-items-center justify-content-between mb-4">
            <h1 class="h3 mb-0 text-gray-800"></h1>
            <Link to="/createuser" class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm"><i
                class="fas fa-fw fa-user-plus"></i>Create User</Link>
        </div>
        <div class="container">
            <div class="row">
                <div class="col-md-12">
                    <div class="card">
                        <div class="card-body">
                            <h5 class="card-title text-uppercase mb-0">List Users</h5>
                        </div>
                        <div class="table-responsive">
                            <table class="table no-wrap user-table mb-0">
                                <thead>
                                    <tr>
                                        <th scope="col" class="border-0 text-uppercase font-medium pl-4">#</th>
                                        <th scope="col" class="border-0 text-uppercase font-medium">Name</th>
                                        <th scope="col" class="border-0 text-uppercase font-medium">Occupation</th>
                                        <th scope="col" class="border-0 text-uppercase font-medium">Email</th>
                                        <th scope="col" class="border-0 text-uppercase font-medium">Phone No.</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                       users.userList.map((users)=> {
                                            // console.log(users.userName)
                                            return <tr>
                                            <td class="pl-4">1</td>
                                            <td>
                                                <Link to="/profile">
                                                    {users.userName}
                                                </Link>
                                            </td>
                                            <td>
                                                <span class="text-muted">{users.userOccupation}</span><br />
                                            </td>
                                            <td>
                                                <span class="text-muted">{users.userEmail}</span><br />
                                            </td>
                                            <td>
                                                <span class="text-muted">{users.userPhone}</span><br />
                                            </td>
                                            <td>
                                                <Link to={`/edituser/${users.userName}`}>
                                                    <button type="button" class="btn btn-outline-info "><i class="fa fas fa-user-edit"></i>Edit</button>
                                                </Link>
                                            </td>
                                        </tr>
    
                                        })
                                    }
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </>
}

export default User;