import React, { useContext, useState } from "react";
import UserContext from "./usercontext";

function CreateUsers() {
    let users = useContext(UserContext);
    let [userName, setuserName] = useState("initial value");
    let [userOccupation, setuserOccupation] = useState("initial value");
    let [userEmail, setuserEmail] = useState("initial value");
    let [userPhone, setuserPhone] = useState("initial value");
    let userData ={
        userName , userOccupation,userEmail,userPhone
    }
    return <>
        <h1>Create User</h1>
        <form onSubmit={(e)=>{
            e.preventDefault();
            // console.log(userData);
            users.setuserList([...users.userList, userData])
        }}>
            <div class="form-group">
                <label for="exampleInputEmail1">Name</label>
                <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Your Name" onChange={(e) => {
                    setuserName(e.target.value);
                }} />

            </div>
            <div class="form-group">
                <label for="exampleInputEmail1">Occupation</label>
                <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Your Occupation" onChange={(e)=>{
                    setuserOccupation(e.target.value);
                }} />
            </div>
            <div class="form-group">
                <label for="exampleInputEmail1">Email address</label>
                <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" onChange={(e)=>{
                    setuserEmail(e.target.value);
                }} />
                <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
            </div>
            <div class="form-group">
                <label for="exampleInputPassword1">Phone No.</label>
                <input type="number" class="form-control" id="exampleInputPassword1" placeholder="Enter Your Phone No." onChange={(e)=>{
                    setuserPhone(e.target.value);
                }} />
            </div>

            <button type="submit" class="btn btn-primary">Submit</button>
        </form>
    </>
}

export default CreateUsers;